.vote-cell {
  width: auto;
  padding-right: 16px;
  vertical-align: top;
  grid-column: 1;
  grid-column-start: 1;
  grid-column-end: auto;
  color:#6a737c;

  .vote  {
    padding: 0;
    margin-bottom: 8px;
    text-align: center;

    .vote-count {
      font-size: 20px;
    }

    .count-text {
      font-size: 12px;
    }
  }
}

.vote-container {
  margin: -2px;
  display: flex;
  align-items: stretch;
  flex-direction: column;
  color: #bbc0c4 !important;

  button {
    padding: 0;
    border: none;
    outline: none;
    font: unset;
    border-radius: 0;
    background: none;
    box-shadow: none;
    text-align: center;
    text-decoration: none;
  }
  .vote-count {
    font-size: 21px;
    display: flex;
    justify-content: center;
    align-items: center;
    color:#6a737c;
  }

}